body {
  margin: 0;
  padding: 0;
  width: 100vw;
  height: 100vh;
}

#root {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
}

.piskel {
  margin: 0 auto;
  padding: 0;
  width: 100%;
  height: 100%;
  background: #ececec;
  display: grid;  
}

.header{
  display: none
}

.main {
  height: 100%;
  display: grid;
  grid-template-columns: 15% 15% 70%;
  color: white;
}

button {
  cursor: pointer;  
}
button:hover { 
  border: 1px solid black;  
 }

